"use client";
import { useState } from "react";
import { Card } from "@/components/ui/card";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { ChevronDown, ChevronUp } from "lucide-react";
import { Button } from "@/components/ui/button";

export default function Statistics() {
  const [view, setView] = useState("week");
  const [dropdownOpen, setDropdownOpen] = useState(false);

  const data = [
  { month: "Jan", thisYear: 240000, lastYear: 180000 },
  { month: "Feb", thisYear: 180000, lastYear: 220000 },
  { month: "Mar", thisYear: 200000, lastYear: 170000 },
  { month: "Apr", thisYear: 220000, lastYear: 200000 },
  { month: "May", thisYear: 260000, lastYear: 230000 },
  { month: "Jun", thisYear: 240000, lastYear: 190000 },
  { month: "Jul", thisYear: 210000, lastYear: 170000 },
  { month: "Aug", thisYear: 230000, lastYear: 180000 },
  { month: "Sep", thisYear: 250000, lastYear: 200000 },
  { month: "Oct", thisYear: 270000, lastYear: 220000 },
  { month: "Nov", thisYear: 280000, lastYear: 230000 },
  { month: "Dec", thisYear: 300000, lastYear: 250000 },
];


  return (
    <div className="w-full flex justify-center mt-8 px-4">
      {/* Container for center alignment */}
      <div className="w-full max-w-10xl">
        <Card
          className="
            p-5 rounded-lg shadow-sm bg-white 
            w-full h-[250px]
          "
        >
          {/* Header */}
          <div className="flex justify-between items-center mb-4 relative">
            {/* Dropdown */}
            <div
              className="flex items-center gap-1 cursor-pointer"
              onClick={() => setDropdownOpen(!dropdownOpen)}
            >
              <h2 className="text-sm font-semibold text-gray-600">
                {view === "month" ? "Monthly Comparison" : "Weekly Comparison"}
              </h2>
              {dropdownOpen ? (
                <ChevronUp size={16} className="text-gray-500" />
              ) : (
                <ChevronDown size={16} className="text-gray-500" />
              )}
            </div>

            {/* Buttons */}
            <div className="flex flex-wrap gap-2 justify-end">
              <Button
                variant={view === "week" ? "default" : "outline"}
                size="sm"
                className={`text-xs px-2 py-1 ${
                  view === "week"
                    ? "bg-[#0d9488] text-white"
                    : "border-green-600 text-green-600"
                }`}
                onClick={() => setView("week")}
              >
                This week
              </Button>
              <Button
                variant={view === "last" ? "default" : "outline"}
                size="sm"
                className={`text-xs px-2 py-1 ${
                  view === "last"
                    ? "bg-[#0d9488] text-white"
                    : "border-green-600 text-green-600"
                }`}
                onClick={() => setView("last")}
              >
                Last week
              </Button>
            </div>

            {/* Dropdown Menu */}
            {dropdownOpen && (
              <div className="absolute top-7 left-0 bg-white shadow-md rounded-md border w-40 text-sm z-10">
                <div
                  className="px-3 py-2 hover:bg-gray-100 cursor-pointer"
                  onClick={() => {
                    setView("week");
                    setDropdownOpen(false);
                  }}
                >
                  Weekly Comparison
                </div>
                <div
                  className="px-3 py-2 hover:bg-gray-100 cursor-pointer"
                  onClick={() => {
                    setView("month");
                    setDropdownOpen(false);
                  }}
                >
                  Monthly Comparison
                </div>
              </div>
            )}
          </div>

          {/* Chart */}
          <ResponsiveContainer width="100%" height="75%">
  <BarChart data={data} margin={{ top: 0, right: 20, left: 40, bottom: 0 }}>
    <CartesianGrid strokeDasharray="3 3" vertical={false} />
    <XAxis dataKey="month" tick={{ fontSize: 12 }} axisLine={false} />
    <YAxis
      tick={{ fontSize: 12 }}
      tickFormatter={(v) => `$${(v / 1000).toFixed(0)}k`}
      axisLine={false}
    />
    <Tooltip
      formatter={(value) => `$${value.toLocaleString()}`}
      contentStyle={{ fontSize: "12px" }}
    />
    <Bar dataKey="lastYear" fill="#e5e7ed" radius={[6, 6, 0, 0]} barSize={20} />
    <Bar dataKey="thisYear" fill="#0d9488" radius={[6, 6, 0, 0]} barSize={20} />
  </BarChart>
</ResponsiveContainer>

        </Card>
      </div>
    </div>
  );
}
